// Typography
$raleway: ralewayregular, sans-serif;

$lato: latoregular, latohairline, latoblack, latolight, latobold, sans-serif;
$lato-italic: latolight_italic, latoitalic, latohairline_italic, latobold_italic, latoblack_italic;

$yanone: yanone_kaffeesatzregular, yanone_kaffeesatzlight, yanone_kaffeesatzextralight, yanone_kaffeesatzbold, sans-serif;


$base-font-family: $raleway;
$heading-font-family: $base-font-family;

// Font Sizes
$base-font-size: 16px;

//Font Weights
$bold: bold;

// Line height
$base-line-height: 1.3;
$heading-line-height: 1.2;

// Other Sizes
$base-border-radius: 3px;
$base-spacing: $base-line-height * 1em;
$small-spacing: $base-spacing / 2;
$base-z-index: 0;

// Colors

// Gray
$light-gray: #aaa;
$medium-gray: #999;
$dark-gray: #333;

// Orange
$orange: #ff9934;
$orange-hover: #8e5721;

// White
$white: rgba(255, 255, 255, 1);

// Blue
$blue: #18d;

// Font Colors
$base-font-color: $dark-gray;
$action-color: $blue;
$headline-color: #567;

// Border
$base-border-color: $light-gray;
$base-border: 1px solid $base-border-color;
$border-color: #9ab;
$border: 1px solid $border-color;

// Background Colors
$base-background-color: #fff;
$secondary-background-color: tint($base-border-color, 75%);

// Forms
$form-box-shadow: inset 0 1px 3px rgba(#000, 0.06);
$form-box-shadow-focus: $form-box-shadow, 0 0 5px adjust-color($action-color, $lightness: -5%, $alpha: -0.3);

// Animations
$base-duration: 150ms;
$base-timing: ease;




